import kotlin.js.Date

class Appointment(
    /**
     * 
     */
    val title: String,
    /**
     *  
     */
    val startedHour: Int,
    /**
     *  
     */
    val endedHour: Int,
    /**
     *  
     */
    val startDate: Date,
    /**
     *  
     */
    val endDate: Date,
) {

    val hourRange: IntRange = startedHour..endedHour
    val dateRange: IntRange = startDate.getDate()..endDate.getDate()
}
